<template>
  <div class="search">
    <van-nav-bar left-text="返回" left-arrow fixed @click-left="back">
      <template #right>
        <div class="home-search">
          <van-search
            v-model="name"
            ref="search"
            show-action
            @search="search"
            placeholder="输入商品名称"
          />
        </div>
      </template>
    </van-nav-bar>
    <!-- 搜索推荐区 -->
    <div class="hot" v-show="!products.length">
      <div class="keyword">
        <p>大家都在搜</p>
        <div
          v-for="(item, index) in hotProduct"
          :key="index"
          @click="gotohot(item)"
        >{{item.name}}</div>
      </div>
      <div class="his">
        <p>历史记录</p>
        <div v-for="(item, index) in Shistory" :key="index" @click="searc(item)">
          <van-icon name="underway-o" />
          {{item}}
        </div>
         <p v-show="Shistory.length!=0" class="clear"><span @click="clearhis">清除历史记录</span></p>
      </div>
    </div>

    <BgBox v-show="products.length">
      <div class="clearfix">
        <div
          class="fl like-item"
          v-for="(item, index) in products"
          :key="index"
          @click="goDetail(item.pid)"
        >
          <ProductItem :item="item"></ProductItem>
        </div>
      </div>
    </BgBox>
  </div>
</template>

<script>
import BgBox from "../components/BgBox.vue";
import ProductItem from "../components/ProductItem.vue";
export default {
  name: "Search",

  components: {
    BgBox,
    ProductItem
  },

  data() {
    return {
      //搜索商品关键字
      name: "",
      //搜索商品数据
      products: [],
      //热卖商品数据
      hotProduct: [],
      //历史记录
      Shistory: []
    };
  },

  created() {
    this.getHotProduct();
    this.getSearchhistory();

    this.$nextTick(() => {
      //获取搜索框
      let searchIpt = this.$refs.search.querySelector('[type="search"]');
      //获取焦点
      searchIpt.focus();
    });
  },

  methods: {
    back() {
      this.$router.go(-1);
    },

    //搜索
    search() {
      if(this.name == ""){
          this.$toast("请输入搜索内容");
          return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          name: this.name
        }
      })
        .then(result => {
          this.Searchhistory(this.name);
          this.$toast.clear();
          if (result.data.code == "Q001") {
            if (result.data.result.length > 0) {
              this.products = result.data.result;
            } else {
              this.products = [];
               this.$toast("无商品信息");
            }
          } else {
            this.$toast("服务器请求出错");
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    //获取热卖推荐商品
    getHotProduct() {
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1
        }
      })
        .then(result => {
          if (result.data.code == 500) {
            this.hotProduct = result.data.result;
          }

        })
        .catch(err => {
          // this.$toast.clear();
        });
    },

    //查看商品详情
    goDetail(pid) {
      this.$router.push({ name: "Detail", params: { pid } });
    },
     //查看商品详情
    gotohot(item) {
      this.Searchhistory(item.name)
      let pid=item.pid
      this.$router.push({ name: "Detail", params: { pid } });
    },

    // 历史记录处理(localstorage存储)
    Searchhistory(text) {
      if(this.Shistory[0]==text){
          return
      }
      this.Shistory.unshift(text);
      this.Shistory = this.Shistory.slice(0, 6);
      localStorage.setItem("Searchhistory", JSON.stringify(this.Shistory));
    },
    getSearchhistory() {
     let SH = JSON.parse(localStorage.getItem("Searchhistory"));
     if (SH) {
        this.Shistory = SH;
      }
    },
    searc(item) {
      this.name = item;
      this.search();
    },
     // 清除历史记录
    clearhis(){
        localStorage.removeItem("Searchhistory")
        this.Shistory=[]
    },
  }
};
</script>

<style lang="less" scoped>
.search {
  height: 500px;
  padding-top: 46px;

  .hot {
    .keyword {
      padding-left: 10px;
      padding-top: 20px;
      padding-bottom: 20px;
      background-color: #fff;
      p {
        margin: 0;
        font-size: 15px;
        color: #999;
      }
      div {
        display: inline-block;
        font-size: 13px;
        height: 34px;
        line-height: 33px;
        padding: 0 12px;
        color: #505050;
        border: 1px solid #ccc;
        border-radius: 17px;
        margin-right: 10px;
        margin-top: 12px;
      }
    }

    .his {
      padding-left: 10px;
      margin-top: 20px;
      background-color: #fff;

      p {
        margin: 0;
        padding-top: 20px;
        font-size: 15px;
        color: #999;
      }
      .van-icon {
        color: #999;
        font-size: 16px;
        margin-right: 8px;
        vertical-align: middle;
      }
      div {
        color: #505050;
        font-size: 14px;
        vertical-align: middle;
        height: 44px;
        line-height: 44px;
        border-bottom: 1px solid #ccc;
      }
      .clear {
        padding-top: 0px;
        height: 46px;
        line-height: 46px;
        text-align: center;
      }
    }
  }

  /deep/ .van-nav-bar .van-icon {
    color: #0c34ba;
  }

  /deep/ .van-nav-bar__text {
    color: #0c34ba;
  }

  /deep/ .van-nav-bar__right {
    width: calc(~"100% - 110px");
  }

  /deep/ .home-search {
    width: 100%;
  }
  /deep/ .home-search .van-search {
    padding: 0;
    border-radius: 17px;
    overflow: hidden;
  }

  /deep/ .home-search .van-icon {
    color: #0c34ba;
  }

  .like-item {
    width: calc(~"100% / 3 - 10px + 10px / 3");
    margin-right: 10px;
    margin-bottom: 10px;
    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}
</style>